<template>
	<div class="pageWrap" :style='"min-height:"+bgWrapHeight+"px;"'>
        <img class='loginArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goPage("/Home")'>
        <div class="orderDetailWrap">
            <img class='loginLogo' src="../assets/images/loginLogo.png" alt="">
            <div class="loginTypeBox flexStart">
                <div class="loginTypeItem loginLeft " :class="loginType == 'Login' ? 'loginActive' : ''" @click="changeType('Login')">Login</div>
                <div class="loginTypeItem loginRight" :class="loginType == 'Register' ? 'loginActive' : ''" @click="changeType('Register')">Register</div>
            </div>
            <div class="loginInputBox" v-if="loginType == 'Login'">
                <div class="loginInputItem" v-if="loginWay == 'cellphone'">
                    <div class="loginTypeLabel flexBetween">
                        Mobile
                        <div class="changeType flexStart" @click="changeWay('email')">
                            <img class='loginExchangeIcon' src="../assets/images/loginExchangeIcon.png" alt="">
                            <p class="changeText">Login by Email</p>
                        </div>
                    </div>
                    <div class="inputOuter flexStart">
                        <img class='inputPhoneIcon' src="../assets/images/inputPhoneIcon.png" alt="">
                        <input class='loginInput' v-model="receiverId" type="text" @input.prevent='changeLoginMobile(receiverId)'>
                        <img class='clearInputIcon' src="../assets/images/clearInputIcon.png" alt="" @click="receiverId = '';showloginErrorMsgText = false;loginUserNamecheck = false;" v-if="receiverId">
                        <span class="inputErrorText" v-if="showloginErrorMsgText">{{loginErrorMsgText}}</span>
                    </div>
                </div>
                <div class="loginInputItem" v-else-if="loginWay == 'email'">
                    <div class="loginTypeLabel flexBetween">
                        Email
                        <div class="changeType flexStart" @click="changeWay('cellphone')">
                            <img class='loginExchangeIcon' src="../assets/images/loginExchangeIcon.png" alt="">
                            <p class="changeText">Login by Mobile</p>
                        </div>
                    </div>
                    <div class="inputOuter flexStart">
                        <img class='inputEmailIcon' src="../assets/images/inputEmailIcon.png" alt="">
                        <input class='loginInput' v-model="receiverId" type="text" @input.prevent='changeLoginEmail(receiverId)'>
                        <img class='clearInputIcon' src="../assets/images/clearInputIcon.png" alt="" @click="receiverId = '';showloginErrorMsgText = false;loginUserNamecheck = false;" v-if="receiverId">
                        <span class="inputErrorText" v-if="showloginErrorMsgText">{{loginErrorMsgText}}</span>
                    </div>
                </div>
                <div class="loginInputItem">
                    <div class="loginTypeLabel">Password</div>
                    <div class="inputOuter flexStart">
                        <img class='inputLockIcon' src="../assets/images/inputLockIcon.png" alt="">
                        <input class='loginInput' v-model="loginPassword" :type="loginPasswordOpen ? 'text':'password'" maxlength="40" @input.prevent="changeloginPassword(loginPassword)">
                        <img class='inputEyeClose' src="../assets/images/inputEyeClose.png" alt="" @click="loginPasswordOpen = !loginPasswordOpen" v-if="!loginPasswordOpen">
                        <img class='inputEyeClose' src="../assets/images/inputEyeOpen.png" alt="" @click="loginPasswordOpen = !loginPasswordOpen" v-else>
                        <span class="inputErrorText" v-if="loginPasswordErrorMsg">{{loginPasswordErrorText}}</span>
                    </div>
                </div>
                <div class="submitBtn_91" :class="loginAllow ? 'GradientAllowBtn' : ''" style="margin-top: 1.24rem;" @click="login()">Continue</div>
                <div class="forgetText">
                    Forgot password?
                    <span style="color:#38ADFF; margin-left: .13rem;">Reset</span>
                </div>
            </div>
            <div class="registerInputBox" v-else-if="loginType == 'Register'">
                <div class="loginInputItem" v-if="registerWay == 'cellphone'">
                    <div class="loginTypeLabel flexBetween">
                        Mobile
                        <div class="changeType flexStart" @click="changeRegisterWay('email')">
                            <img class='loginExchangeIcon' src="../assets/images/loginExchangeIcon.png" alt="">
                            <p class="changeText">Register by Email</p>
                        </div>
                    </div>
                    <div class="inputOuter flexStart">
                        <img class='inputPhoneIcon' src="../assets/images/inputPhoneIcon.png" alt="">
                        <input class='loginInput' v-model="receiverId" type="text" maxlength="12" @input.prevent="changeRegisterMobile(receiverId)">
                        <img class='clearInputIcon' src="../assets/images/clearInputIcon.png" alt="" @click="receiverId = '';showSignErrorMsgText = false;signUpUserNamecheck = false;" v-if="receiverId">
                        <span class="inputErrorText" v-if="showSignErrorMsgText">{{signErrorMsgText}}</span>
                    </div>
                </div>
                <div class="loginInputItem" v-else-if="registerWay == 'email'">
                    <div class="loginTypeLabel flexBetween">
                        Email
                        <div class="changeType flexStart" @click="changeRegisterWay('cellphone')">
                            <img class='loginExchangeIcon' src="../assets/images/loginExchangeIcon.png" alt="">
                            <p class="changeText">Register by Mobile</p>
                        </div>
                    </div>
                    <div class="inputOuter flexStart">
                        <img class='inputEmailIcon' src="../assets/images/inputEmailIcon.png" alt="">
                        <input class='loginInput' v-model="receiverId" type="text" maxlength="40" @input.prevent="changeRegisterEmail(receiverId)">
                        <img class='clearInputIcon' src="../assets/images/clearInputIcon.png" alt="" @click="receiverId = '';showSignErrorMsgText = false;signUpUserNamecheck = false;" v-if="receiverId">
                        <span class="inputErrorText" v-if="showSignErrorMsgText">{{signErrorMsgText}}</span>
                    </div>
                </div>
                <div class="loginInputItem">
                    <div class="loginTypeLabel">Password</div>
                    <div class="inputOuter flexStart">
                        <img class='inputLockIcon' src="../assets/images/inputLockIcon.png" alt="">
                        <input class='loginInput' v-model="registerPassword" :type="registerPasswordOpen ? 'text':'password'" maxlength="40" @input.prevent="changeRegisterPassword(registerPassword)">
                        <img class='inputEyeClose' src="../assets/images/inputEyeClose.png" alt="" @click="registerPasswordOpen = !registerPasswordOpen" v-if="!registerPasswordOpen">
                        <img class='inputEyeClose' src="../assets/images/inputEyeOpen.png" alt="" @click="registerPasswordOpen = !registerPasswordOpen" v-else>
                        <span class="inputErrorText" v-if="registerPasswordErrorMsg">{{registerPasswordErrorText}}</span>
                    </div>
                </div>
                <div class="loginInputItem">
                    <div class="loginTypeLabel">Referral code (Optional)</div>
                    <div class="inputOuter flexStart">
                        <input v-if="!VHinvite_code" class='loginInput' v-model="referralCode" type="text">
                        <div v-else class='loginInput flexStart'>{{VHinvite_code}}</div>
                    </div>
                </div>
                <div class="registerAgreeTextBox flexStart">
                    <img class='agreeIcon' src="../assets/images/loginEmptySelect.png" alt="" v-if="!agreeText" @click="agreeText = !agreeText">
                    <img class='agreeIcon' src="../assets/images/loginYesSelect.png" alt="" v-else @click="agreeText = !agreeText">
                    I agree to the <span> Terms of Use </span> and <span> Privacy Policy </span>
                </div>
                <div class="submitBtn_91" :class="registerAllow ? 'GradientAllowBtn' : ''" style="margin-top: .52rem;" @click="checkRegisterMsg()">Continue</div>
                <div class="forgetText flexCenter" style="padding-bottom: 1.2rem;">
                    <img class='loginHelpIcon' src="../assets/images/loginHelpIcon.png" alt="">
                    Have any problems?
                    <span style="color:#38ADFF; margin-left: .13rem;">Contact us</span>
                </div>
            </div>
        </div>
        <van-dialog class='verificationBox' v-model="verificationBox" :showConfirmButton='false'>
            <template #default>
                <div class="rechargeTitBox">
                    Verification
                    <img class='componentCloseIcon' @click="verificationBox = false" style="" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="verifcationMain">
                    <p class='inputTopScript'>Enter {{registerWay == 'cellphone' ? 'mobile' : 'email'}} verification code</p>
                    <div class="verificationInputWrap flexBetween">
                        <div class="verifcationInputOuter" :class='inputActive == "input6" ? "blueBorder":""' style="width: 3.99rem;">
                            <input class='normalInput' v-model="code" type="number" maxlength="8" @focus="focusInput('input6')" @blur='blurInput("1")'>
                        </div>
                        <div class="sendCodeBtn" :class='isCansend ? "blueSendBtn":""' @click="sendCaptcha()">{{codeText}}</div>
                    </div>
                    <p class="inputBotScript" v-if="!wrongCodeAlert">Code will be sent to {{userNameEP}}</p>
                    <p class="inputBotScript" style="color:#FF305F;" v-else>Wrong code</p>
                    <div class="submitBtn_91 " :class="code && sendTimes > 0 ? 'GradientAllowBtn' : ''" style="margin-top: .57rem;" @click="accountNumberCreate()">Confirm</div>
                </div>
            </template>
        </van-dialog>
        <van-dialog class='alertModelBox' v-model="alertModelBox" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Error
                    <img class='alertModelCloseIcon' @click="alertModelBox = false" style="" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="alertBoxMain">
                    <p class="alertContent">{{alertContent}}</p>
                    <div class="btnBox flexBetween" style="margin-top: .57rem;">
                        <div class="submitBtn_91 blueBorderBtn" style="width: 2.9rem;" @click="alertModelBox = false;">Change</div>
                        <div class="submitBtn_91 blueAllowBtn" style="width: 2.9rem;" @click="alertModelBox = false;changeType('Login');">Login</div>
                    </div>
                </div>
            </template>
        </van-dialog>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'Login',
		data: function(){
			return {
                VHinvite_code: '',
                showLoading: true,
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                loginType: 'Login',
                loginWay: 'cellphone',
                registerWay: 'cellphone',
                referralCode: '',
                agreeText: false,
                registerPasswordOpen: false,
                inputActive: '',
                loginPasswordOpen: false,
                alertModelBox: false,
                alertContent: '',

                verificationBox: false,
                code: '',
                userNameEP: '',
                codeText: 'Send',
                sendTimes: 0,
                isCansend: true,
                timer: null,
                wrongCodeAlert: false,
                wrongCodeText: '',

                receiverId: '',
                showSignErrorMsgText : false,
                signErrorMsgText : '',
                signUpUserNamecheck : false,

                registerPassword: '',
                registerPasswordErrorMsg : false,
                registerPasswordErrorText : '',
                registerPasswordcheck : false,

                showloginErrorMsgText: false,
                loginErrorMsgText: '',
                loginUserNamecheck: false,

                loginPassword: '',
                loginPasswordErrorMsg : false,
                loginPasswordErrorText : '',
                loginPasswordcheck : false,
			}
		},
		created(){
			// this.getData()
            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				console.log(localStorage.getItem("VHinvite_code"))
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			this.bgWrapHeight = viwePortHeight 
		},
		methods: {
            login: function(){
                if(this.receiverId.length != 6) {
                    if(!this.loginAllow) {
                        return
                    }
                }
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params ={
                    'receiverType': this.loginWay, 
                    'receiverId': this.receiverId,
                    'password': this.loginPassword,
                    'clientType': 'H5',
                }
                https.fetchPost('/api/user/auth/login',params ).then((data) => {
                    let res = data.data
                    this.showLoading = false
                    if(res.code == 0) {
                        this.$toast.clear()
                        localStorage.setItem('user-token',res.data.token)
                        localStorage.setItem('userId',res.data.id)
                        this.goPage('/Home')
                    }else {
                        this.$toast({
                            message: res.msg,
                            forbidClick: true,
                            icon: require('../assets/images/errorIcon.png'),
                            duration: 2000,
                            className: 'resultToast'
                        });
                    }
                }).catch(err=>{
                    this.showLoading = false
                    console.log(err)
                    this.$toast({
                        message: https.NetworkErrorText,
                        forbidClick: true,
                        icon: require('../assets/images/errorIcon.png'),
                        duration: 2000,
                        className: 'resultToast'
                    });
                })
            },
			accountNumberCreate: function(){
                if(!this.code || this.sendTimes <= 0) {
                    return
                }
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params ={
                    'receiverType': this.registerWay, 
                    'receiverId': this.receiverId,
                    'captcha': this.code,
                    'password': this.registerPassword,
                    'referralCode': this.VHinvite_code ? this.VHinvite_code : this.referralId,
                    'clientType': 'H5'
                }
                https.fetchPost('/api/user/register/create',params ).then((data) => {
                    let res = data.data
                    this.showLoading = false
                    if(res.code == 0) {
                        this.$toast({
                            message: 'Successfully registered',
                            forbidClick: true,
                            icon: require('../assets/images/successIcon.png'),
                            duration: 2000,
                            className: 'resultToast'
                        });
                        setTimeout(() => {
                            this.verificationBox = false
                            this.loginWay = this.registerWay
                            this.loginPassword = this.registerPassword
                            this.loginUserNamecheck = true
                            this.loginPasswordcheck = true
                            this.login()
                        }, 2000);
                    }else if(res.code == -7){
                        this.alertContent = res.msg
                        clearInterval(this.timer)
                        this.sendTimes = 0
                        this.isCansend = true
                        this.verificationBox = false
                        this.alertModelBox = true
                        // if(res.msg == 'Wrong code') {
                        //     this.wrongCodeAlert = true
                        // }
                        // this.$toast({
                        //     message: res.msg,
                        //     forbidClick: true,
                        //     icon: require('../assets/images/errorIcon.png'),
                        //     duration: 2000,
                        //     className: 'resultToast'
                        // });
                    }else {
                        if(res.msg == 'Wrong code') {
                            this.wrongCodeAlert = true
                        }
                        this.$toast({
                            message: res.msg,
                            forbidClick: true,
                            icon: require('../assets/images/errorIcon.png'),
                            duration: 2000,
                            className: 'resultToast'
                        });
                    }
                }).catch(err=>{
                    this.showLoading = false
                    console.log(err)
                    this.$toast({
                        message: https.NetworkErrorText,
                        forbidClick: true,
                        icon: require('../assets/images/errorIcon.png'),
                        duration: 2000,
                        className: 'resultToast'
                    });
                })

            },
            checkRegisterMsg(){
                if(!this.registerAllow) {
                    return
                }
                let params = {
                    'receiverType': this.registerWay, 
                    'receiverId': this.receiverId,
                    'password': this.registerPassword, 
                    'referralCode': this.referralCode,
                }
                console.log(params)
                https.fetchPost('/api/user/register/check',params).then((data) => {
                    let res = data.data
                    if(res.code == 0) {
                        this.registerContinue()
                    }else {
                        if(res.data && res.data.errorField) {
                            if(res.data.errorField == 'receiverId') {
                                this.showSignErrorMsgText = true
                                this.signErrorMsgText = data.data.msg
                                this.signUpUserNamecheck = false
                            }else if(res.data.errorField == 'password') {
                                this.registerPasswordErrorMsg = true
                                this.registerPasswordErrorText = data.data.msg
                                this.registerPasswordcheck = false
                            }
                        }
                    }
                }).catch(err=>{
                    this.isCansend = true
                    console.log(err)
                    this.$toast({
                        message: https.NetworkErrorText,
                        forbidClick: true,
                        icon: require('../assets/images/errorIcon.png'),
                        duration: 2000,
                        className: 'resultToast'
                    });
                })
            },
            registerContinue: function(){
                if(this.registerWay == 'cellphone') {
                    let starLength = this.receiverId.length - 4
                    let newStrStart = this.receiverId.slice(0,2)
                    let newStrEnd = this.receiverId.slice(-2)
                    let starStr = ''
                    for (let index = 0; index < starLength; index++) {
                        starStr = starStr+'*'
                    }
                    this.userNameEP = newStrStart + starStr + newStrEnd
                }else {
                    let beforStr = this.receiverId.split('@')[0]
                    let afterStr = this.receiverId.split('@')[1]

                    console.log(beforStr)
                    console.log(afterStr)
                    console.log(afterStr.length)
                    let starStr1 = ''
                    let starStr2 = ''
                    let starLength1 = beforStr.length - 1
                    let starLength2 = 0
                    let newStrStart = this.receiverId.slice(0,1)
                    let newStrEnd = ''
                    for (let index = 0; index < starLength1; index++) {
                        starStr1 = starStr1+'*'
                    }
                    if(afterStr.length > 3) {
                        starLength2 = afterStr.length - 4
                        for (let index = 0; index < starLength2; index++) {
                            starStr2 = starStr2+'*'
                        }
                        newStrEnd = this.receiverId.slice(-4)
                    }else {
                        starStr2 = ''
                        newStrEnd = this.receiverId.slice(0-afterStr.length)
                    }
                    
                    this.userNameEP = newStrStart + starStr1 + '@' + starStr2 + newStrEnd
                }
                this.code = ''
                this.sendTimes = 0
                this.codeText = 'Send'
                this.isCansend = true
                clearInterval(this.timer)
                this.verificationBox = true
            },
            sendCaptcha: function(){
                if(!this.isCansend) {
                    this.$toast({
                        message: 'Try again later',
                        forbidClick: true,
                        duration: 2000,
                        className: 'smallToast'
                    });
                    return
                }
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                this.isCansend = false
                let params = {
                    'receiverType': this.registerWay, 
                    'receiverId': this.receiverId,

                }
                console.log(params)
                https.fetchPost('/api/user/register/captcha/send',params).then((data) => {
                    let res = data.data
                    // console.log(result)
                    let time;
                    this.showLoading = false
                    this.$toast.clear()
                    if(res.code == 0) {
                        if(this.sendTimes < 2) {
                            time = 60 + (30*this.sendTimes)
                        }else if(this.sendTimes > 2 && this.sendTimes < 5) {
                            time = time * 2
                        }else {
                            time = 960
                        }
                        this.sendTimes++
                        this.isCansend = false
                        this.codeText = time+'s'
                        this.timer = setInterval(() => {
                            time--
                            this.codeText = time+'s'
                            if(time == 1) {
                                this.isCansend = true
                                this.codeText = 'Send'
                                clearInterval(this.timer)
                            }
                        }, 1000);
                    }else {
                        this.isCansend = true
                        this.$toast({
                            message: res.msg,
                            forbidClick: true,
                            icon: require('../assets/images/errorIcon.png'),
                            duration: 2000,
                            className: 'resultToast'
                        });
                    }
                }).catch(err=>{
                    this.showLoading = false
                    this.isCansend = true
                    console.log(err)
                    this.$toast({
                        message: https.NetworkErrorText,
                        forbidClick: true,
                        icon: require('../assets/images/errorIcon.png'),
                        duration: 2000,
                        className: 'resultToast'
                    });
                })
            },

            changeType(type){
                this.loginType = type
                this.receiverId = ''
                this.showloginErrorMsgText = false
                this.loginErrorMsgText = ''
                this.loginUserNamecheck = false
                this.loginPassword = ''
                this.loginPasswordErrorMsg = false
                this.registerPasswordErrorMsg = false
                this.showSignErrorMsgText = false
                this.registerPassword = ''
            },
            changeWay(way){
                this.loginWay = way
                this.receiverId = ''
                this.showloginErrorMsgText = false
                this.loginErrorMsgText = ''
                this.loginUserNamecheck = false
            },
            changeRegisterWay(way){
                this.registerWay = way
                this.receiverId = ''
                this.showSignErrorMsgText = false
                this.signErrorMsgText = ''
                this.signUpUserNamecheck = false
            },
            changeRegisterPassword(registerPassword) {
                let tem=0;
                for(let i=0;i<registerPassword.length;i++){
                    if(registerPassword.charAt(i) == registerPassword.charAt(0)){
                        tem++
                    }
                }
                if(registerPassword.length == tem){
                    this.registerPasswordErrorMsg = true
                    this.registerPasswordErrorText = 'Cannot use all the same characters'
                    this.registerPasswordcheck = false
                }else if(registerPassword.length < 6) {
                    this.registerPasswordErrorMsg = true
                    this.registerPasswordErrorText = 'At least 6 characters'
                    this.registerPasswordcheck = false
                }else if(/^[0-9]+$/.test(registerPassword)) {
                    this.registerPasswordErrorMsg = true
                    this.registerPasswordErrorText = 'Cannot contain only numbers'
                    this.registerPasswordcheck = false
                }else {
                    this.registerPasswordErrorMsg = false
                    this.registerPasswordcheck = true
                }
            },
            changeRegisterMobile(registerMobile){
                let rge = /^[0-9]+$/g;
                if(!rge.test(registerMobile)) {
                    this.showSignErrorMsgText = true
					this.signErrorMsgText = 'Invalid mobile number'
                    this.signUpUserNamecheck = false
                }else if(registerMobile.length < 8 || registerMobile.length > 12){
					this.showSignErrorMsgText = true
					this.signErrorMsgText = 'Invalid mobile number'
                    this.signUpUserNamecheck = false
                }else {
                    this.showSignErrorMsgText = false
					this.signUpUserNamecheck = true
                }
            },
            changeRegisterEmail(registerEmail){
                let reg = /^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/
                if(!reg.test(registerEmail)) {
                    this.showSignErrorMsgText = true
					this.signErrorMsgText = 'Invalid email address'
                    this.signUpUserNamecheck = false
                }else if(registerEmail.length < 6){
                    this.showSignErrorMsgText = true
					this.signErrorMsgText = 'Invalid email address'
                    this.signUpUserNamecheck = false
                }else {
					this.signUpUserNamecheck = true
                    this.showSignErrorMsgText = false
                }
            },
            changeLoginMobile(loginMobile){
				let rge = /^[0-9]+$/g;
                if(!rge.test(loginMobile)) {
                    this.showloginErrorMsgText = true
					this.loginErrorMsgText = 'Invalid mobile number'
                    this.loginUserNamecheck = false
                }else if(loginMobile.length < 8 || loginMobile.length > 12){
					this.showloginErrorMsgText = true
					this.loginErrorMsgText = 'Invalid mobile number'
                    this.loginUserNamecheck = false
                }else {
                    this.showloginErrorMsgText = false
                    this.loginUserNamecheck = true
                }
            },
            changeLoginEmail(LoginEmail){
                let reg = /^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/
                if(!reg.test(LoginEmail)) {
                    this.showloginErrorMsgText = true
					this.loginErrorMsgText = 'Invalid email address'
                    this.loginUserNamecheck = false
                }else if(LoginEmail.length < 6){
                    this.showloginErrorMsgText = true
					this.loginErrorMsgText = 'Invalid email address'
                    this.loginUserNamecheck = false
                }else {
                    this.showloginErrorMsgText = false
					this.loginUserNamecheck = true
                }
            },
            changeloginPassword(loginPassword) {
                let tem=0;
                for(let i=0;i<loginPassword.length;i++){
                    if(loginPassword.charAt(i) == loginPassword.charAt(0)){
                        tem++
                    }
                }
                if(loginPassword.length == tem){
                    this.loginPasswordErrorMsg = true
                    this.loginPasswordErrorText = 'Cannot use all the same characters'
                    this.loginPasswordcheck = false
                }else if(loginPassword.length < 6) {
                    this.loginPasswordErrorMsg = true
                    this.loginPasswordErrorText = 'At least 6 characters'
                    this.loginPasswordcheck = false
                }else if(/^[0-9]+$/.test(loginPassword)) {
                    this.loginPasswordErrorMsg = true
                    this.loginPasswordErrorText = 'Cannot contain only numbers'
                    this.loginPasswordcheck = false
                }else {
                    this.loginPasswordErrorMsg = false
                    this.loginPasswordcheck = true
                }
            },
            focusInput(name){
                this.inputActive = name
            },
            blurInput(name){
                this.inputActive = name
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		},
        computed: {
            registerAllow(){
                if(this.signUpUserNamecheck && this.registerPasswordcheck && this.agreeText) {
                    return true
                }else {
                    return false
                }
            },
            loginAllow(){
                if(this.loginUserNamecheck && this.loginPasswordcheck) {
                    return true
                }else {
                    return false
                }
            }
        }
	}
</script>
